___CSS_LOADER_EXPORT___.push([module.id, `.goods-list-container {
    width: 450px;
    padding: 20px;
    background-color: #1C2541;
    color: black;
    border-radius: 15px;
  }
  
  .category-buttons {
    display: flex;
    justify-content: space-around;
    margin-bottom: 20px;
  }
  
  .category-button {
    background-color: #5BC0BE;
    color: #1C2541;
    border: none;
    padding: 10px 20px;
    border-radius: 40px;
    cursor: pointer;
    font-weight: bold;
  }
  
  .category-button:hover {
    background-color: #FFFFFF;
    color: #000000;
  }
  
  .goods-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  
  }
  
  .goods-item {
    width: 45%;
    margin-bottom: 20px;
    text-align: center;
    cursor: pointer;
    background-color: white;
    text-size-adjust: 1.0em;
    border-radius: 15px;
  }
  
  .goods-item img {
    width: 100%;
    border-top-left-radius: 15px; /* 상단 왼쪽 모서리 둥글게 */
    border-top-right-radius: 15px; /* 상단 오른쪽 모서리 둥글게 */
  }
  
  .goods-item p {
    margin: 10px 0 0 0;
    font-size: 0.9em;
  }
  
  .goods-item p span {
    display: block;
    font-size: 0.8em;
    color: #aaa;
  }
  
